.user-item {
  margin: 2rem;
  width: calc(45% - 2rem);
  min-width: 17.5rem;
  
}

.user-item a {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  padding: 1rem;
  color: white;
  background: #292929;
  
  
}
.verified{
  height: 20px;
  width: 20px;
  margin-left: 20px;
}

.user-item a:hover,
.user-item a:active {
  background: #ffd900;
}
button:hover{
  background-color:black;
}

.user-item__content {
  margin-top: 500px;
  padding: 0;
  box-sizing: border-box;
  
}

.user-item__image {
  width: 4rem;
  height: 4rem;
  margin-right: 1rem;
  

}

.user-item__info h2 {
  font-size: 1.5rem;
  margin: 0 0 0.5rem 10px;
  font-weight: normal;
  color: #ffd900;
  display: inline-block;
}

.user-item:hover h2,
.user-item:active h2,
.user-item:hover h3,
.user-item:active h3 {
  color: #292929;
}

.user-item__info h3 {
  margin: 10px;
}