.form {
    display: flex;
    border-top: 2px solid #d3d3d3;
    flex-direction: row;
  }
  
  .input {
    border: none;
    border-radius: 0;
    width: 80%;
    font-size: 1.2em;
  }
  
  input:focus,
  textarea:focus,
  select:focus {
    outline: none;
  }
  
  .sendButton {
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #2979ff;
    padding: 20px;
    display: inline-block;
    border: none;
    width: 20%;
  }